	module.exports = {"data":{"wordpressPost":{"title":"War Veterans, OTC Medications and Dementia: Most read articles in September","content":"<p>The most-read article in September was once again, Carpenter <em>et al</em>&#8216;s article on Bed sharing and the risk of SIDS. Wakefield <em>et al</em>&#8216;s study of Australian packaging policy stayed in the top ten for another month.  Other newly-published papers in the top ten include Brabers <em>et al</em>&#8216;s survey investigating consumers&#8217; attitudes towards over the counter medication.</p>\n<table width=\"483\" border=\"1\" cellspacing=\"0\" cellpadding=\"0\">\n<tbody>\n<tr>\n<td valign=\"top\" width=\"50\">Rank</td>\n<td valign=\"top\" width=\"132\">Author(s)</td>\n<td valign=\"top\" width=\"301\">Title</td>\n</tr>\n<tr>\n<td width=\"50\">1</td>\n<td width=\"132\">Carpenter <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/5/e002299.full\" target=\"_blank\">Bed sharing when parents do not smoke: is there a risk of SIDS? An individual level analysis of five major case–control studies</a></td>\n</tr>\n<tr>\n<td width=\"50\">2</td>\n<td width=\"132\">Billings <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/8/e003352.full\" target=\"_blank\">Choosing a model to predict hospital admission: an observational study of new variants of predictive models for case finding</a></td>\n</tr>\n<tr>\n<td width=\"50\">3</td>\n<td width=\"132\">McBride <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/9/e003379.full\" target=\"_blank\">The mortality and cancer experience of New Zealand Vietnam war veterans: a cohort study</a></td>\n</tr>\n<tr>\n<td width=\"50\">4</td>\n<td width=\"132\">Shaw <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/9/e003341.full\" target=\"_blank\">The work of commissioning: a multisite case study of healthcare commissioning in England&#8217;s NHS</a></td>\n</tr>\n<tr>\n<td width=\"50\">5</td>\n<td width=\"132\">Hope <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/9/e003207.full\" target=\"_blank\">Prevalence of, and risk factors for, HIV, hepatitis B and C infections among men who inject image and performance enhancing drugs: a cross-sectional study</a></td>\n</tr>\n<tr>\n<td width=\"50\">6</td>\n<td width=\"132\">Ford <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/3/e002269.short\" target=\"_blank\">Current treatments in diabetic macular oedema: systematic review and meta-analysis</a></td>\n</tr>\n<tr>\n<td width=\"50\">7</td>\n<td width=\"132\">Brabers <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/9/e003455.full\" target=\"_blank\">Where to buy OTC medications? A cross-sectional survey investigating consumers’ confidence in over-the-counter (OTC) skills and their attitudes towards the availability of OTC painkillers</a></td>\n</tr>\n<tr>\n<td width=\"50\">8</td>\n<td width=\"132\">Griffiths <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://www.bmjopen.bmj.com/content/3/8/e002893\" target=\"_blank\">How active are our children? Findings from the Millennium Cohort Study</a></td>\n</tr>\n<tr>\n<td width=\"50\">9</td>\n<td width=\"132\">Wakefield <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/7/e003175.full\" target=\"_blank\">Introduction effects of the Australian plain packaging policy on adult smokers: a cross-sectional study</a></td>\n</tr>\n<tr>\n<td width=\"50\">10</td>\n<td width=\"132\">Gao <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://www.bmjopen.bmj.com/content/3/7/e002881.full\" target=\"_blank\">Effects of centrally acting ACE inhibitors on the rate of cognitive decline in dementia</a></td>\n</tr>\n</tbody>\n</table>\n<p>&nbsp;</p>\n<p>Most read figures are based on pdf downloads and full text views. Abstract views are excluded.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"45de6698-3ce4-5dcd-853d-8a9013da9dc3"}}

